@import 'globals/variables';

// study header section
.account-header-wrapper {
  background-color: $white;
  display: flex;

  .account-header {
    align-items: center;
    justify-content: center;
    margin-top: 7rem;

    .profile,
    .profile-illustration {
      margin: 0;
    }

    .profile {
      align-items: center;

      .profile-info {
        span {
          font-size: 31px;
          font-weight: 700;
        }

        p {
          font-size: 17px;
          font-weight: 500;
        }
      }
    }

    .profile-illustration {
      margin-left: auto;
      margin-right: 0;
    }
  }
}

// header divider
hr {
  margin-top: 0;
  margin-bottom: 0;
  background-color: transparent;
  border-top: 0.5px solid $light-grey;
}

.account-navigation {
  width: 68%;
  align-items: flex-start;

  button.btn {
    width: 25%;
    margin-right: 1rem;
    font-size: 18px;
    font-weight: 700;
    border: 1px solid $brand-purple;
    background-color: $white;

    &:hover,
    &:active {
      background-color: $brand-purple;
      color: $white;
    }
  }
}

// study set cards
.study-set-header {
  display: flex;
  flex-direction: column;

  .study-set-wrapper {
    margin-top: 1rem;
    margin-bottom: 1rem;
    margin-left: 0;

    .study-set-col-1 {
      display: flex;
      flex-direction: row;
      //   align-items: center;
      margin-top: 1rem;
      padding-left: 0;

      .icon-title {
        // align-items: center;
        // justify-content: center;

        img {
          width: 112px;
          height: 112px;
          margin-top: 0rem;
          margin-right: 1rem;
        }
        h2 {
          margin-top: 0 !important;
          margin-left: 0;
          padding-left: 0;
          font-size: 22px;
          text-align: left;
        }
      }

      button {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 0;
        margin-right: 2rem;
        margin-bottom: 0.5rem;
        font-size: 30px;
        font-weight: 700;
        background-color: $white;
        height: 266px;
        width: 505px;
        box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);
        border-radius: 10px;

        &:hover {
          color: $white;
          background-color: $brand-purple;
        }

        &:hover .circle {
          color: $brand-purple;
          background-color: $white;
        }

        .circle {
          font-size: 22px;
          border-radius: 50%;
          height: 42px;
          width: 42px;
          color: $white;
          background-color: $brand-purple;
        }

        .flip-icon {
          position: absolute;
          top: 12px;
          margin-left: 453px;
        }
      }
    }
  }
}
