@import 'globals/variables';

/*  -- navbar --  */
.navbar-nav {
  height: 86px;
  padding-top: 1.5rem;
}

/* -- logo -- */
.navbar {
  height: 87px;
  display: flex;
}
.bg-dark {
  background-color: $brand-black !important;
}
.navbar-brand {
  margin-right: 3rem;
  margin-left: 2rem;
}
.navbar-dark .navbar-nav .nav-link {
  color: $white;
}
.nav-item {
  margin-right: 2rem;
}
.nav-link {
  font-size: 18px;
}
.nav-item.active {
  border-bottom: 4px solid $brand-green;
}

/* -- user login --  */
.user-login {
  .nav-link {
    font-weight: 500;
    color: $white;
  }
  .nav-item {
    margin-right: 0;
  }
  .navbar-avatar {
    margin-left: 0.5rem;
    margin-right: 2.6rem;
  }
}
