@import 'globals/variables';

.side-menu-btn {
    position:absolute;
    width:2rem;
    height:2rem;
    top: 0;
    left: 0;
    background-color: red;
    border: 1px red solid;
    z-index:99;
  }
  .sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 99;
    top: 86px;
    left: 0;
    background-color: #000;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
    
    a {
      padding: 8px 8px 8px 32px;
      text-decoration: none;
      font-size: 1rem;
      color: #818181;
      display: block;
      transition: 0.3s;
    }
    
    &:hover {
      color: #f1f1f1;
    }
    
    .closebtn {
      position: absolute;
      top: 0;
      right: 25px;
      font-size: 36px;
      margin-left: 50px;
    }
    
    @media screen and (max-height: 450px) {
      .sidenav {padding-top: 15px;}
      .sidenav a {font-size: 18px;}
    }
  }
  