@import 'globals/variables';


/* -- main header -- */
.main-header {
  background-color: $white !important;


  .container {
    margin-top: 14px;
    
    h2{
      padding: 0 0;
      margin-top: 58px;
      font-size: 48px;
      font-weight: 700;
    }
  }
}

/* -- main section -- */
.main-content {
  background-color: $light-grey-background;
  padding-bottom: 6rem;

  /* -- steps section -- */
  .steps-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: space-around;

    .step-columns {
      flex: 3;
      flex-flow: row wrap;
      justify-content: space-around;
      margin: auto;

      .steps {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-width: 9rem;
      }
      .step-circle {
        background-color: $light-grey;
        border-radius: 50%;
        height: 68px;
        width: 68px;
      }
      .step-circle.active {
        background-color: $brand-purple;
      }
    }
  }

  /* -- new tech image --  */
  .new-tech-img-wrapper {
    flex: 3;

    .new-tech-img {
      display: flex;
      margin-top: -8rem;
      z-index: 10;
      margin-left: 4rem;
    }
  }

  /*  -- summary section --  */
  .summary-section {
    width: 100%;
    top: 592px;
    margin-top: 2rem;
    padding-bottom: 2rem;
    border-radius: 0.5rem;
    background-color: $white;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);
    
    .summary-header h2{
      margin-top: 58px;
      padding-left: 1.8rem;
      font-size: 48px;
      font-weight: 700;
    }
    .summary-wrapper {
      display: flex;
      box-shadow: none;
    }
    .summary-col-1 {
      flex: 1;
      width: 50%;
    }
    .summary-col-2 {
      flex: 1;
      width: 50%;
    }
  }

  /*  -- five star rating --  */
  .rating-wrapper {
    margin-left: -15px;
    
    .stars {
      --percent: calc(var(--rating) / 5 * 100%);
      
      display: inline-block;
      font-size: var(--star-size);
      font-family: Times; // make sure ★ appears correctly
      line-height: 1;
      
      &::before {
        content: '★★★★★';
        letter-spacing: 3px;
        background: linear-gradient(90deg, var(--star-background) var(--percent), var(--star-color) var(--percent));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }

  .btn-primary {
    color: $brand-black;
    border-radius: 5px;
    font-weight: bold;
    width: 15rem;
    margin-right: 1rem;
    background-color: $white !important;
    border-color: $brand-green !important;
    box-shadow: 3px 3px 0 0 $brand-green;
    
    &:hover {
      color: $white;
      background-color: $brand-green !important;
      box-shadow: 3px 3px 0 0 $brand-black;
    }
  }

  .btn-secondary {
    color: $brand-black;
    font-weight: bold;
    border-radius: 5px;
    width: 15rem;
    background-color: $white !important;
    border-color: $brand-black !important;
    box-shadow: 3px 3px 0 0 $brand-black;

    &:hover {
      color: $white;
      background-color: $brand-black !important;
    }
  }
}
