@import 'globals/variables';

/*  -- footer --  */
footer {
    position: absolute;
    display: flex;
    width: 100%;
    height: 428px;
    background-color: $brand-black;
    z-index: 1;
  
    ul li {
      padding: 5px 0px;
    }
    .info span,
    .legal span,
    .contact span {
      color: $white;
      font-weight: 500;
      padding-bottom: 0px;
      margin-top: 1rem;
      display: block;
      font-size: 22px;
      letter-spacing: 1px;
    }
    .info li a,
    .legal li a,
    .contact li a {
      color: $white;
      font-weight: 500;
      letter-spacing: 2px;
      text-decoration: none;
      font-size: 15px;
      letter-spacing: 1px;
    }
    .contact li {
      float: left;
    }
    .info,
    .legal,
    .contact {
      margin-top: 4rem;
      list-style: none;
    }
  
    .copyright {
      position: absolute;
      font-weight: 700;
      bottom: 0px;
      top: 380px;
      left: -1rem;
      font-size: 13px;
      letter-spacing: -0.5px;
      z-index: 2;
    }
  
    .footer-img {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: auto;
    }
  }