@import 'globals/variables';

// study header section
.main-header {
  background-color: $light-grey-background !important;
}

.study-header {
  h3 {
    font-size: 24px;
    font-weight: 500;
  }

  .icon-title {
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 113px;
      height: 113px;
      margin-top: 0rem;
      margin-right: 1rem;
    }
    h2 {
      margin-top: 0 !important;
      margin-left: 0;
      padding-left: 0;
    }
  }

  h4 {
    font-size: 30px;
    font-weight: 700;
  }

  h5 {
    margin-top: 3rem;
    margin-bottom: 0;
  }

  .prerequisites {
    font-size: 24px;
    font-weight: bold;

    .topic {
      font-size: 24px;
      font-weight: 500;
      color: $dark-grey;
    }
  }

  .authors {
    font-size: 20px;
    font-weight: 400;
    color: $dark-grey;
  }

  .topic-filters {
    background-color: $brand-black;
    width: 10%;
    height: 2rem;
    line-height: 1rem;
    color: white;
    margin-left: 2rem;
  }

  .study-navigation {
    width: 68%;
    align-items: flex-start;

    button.btn {
      width: 25%;
      margin-right: 1rem;
      font-size: 18px;
      font-weight: 700;
      border: 1px solid $brand-purple;
      background-color: $white;

      &:hover,
      &:active {
        background-color: $brand-purple;
        color: $white;
      }
    }
  }
}

// header divider
hr {
  background-color: transparent;
  border-top: 0.5px solid $light-grey;
}

.summary-wrapper {
  margin-bottom: 1rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
  width: 100%;
  border-radius: 0.5rem;
  background-color: $white;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);

  p {
    font-weight: 400;
    font-size: 22;
  }
}
