@import 'globals/variables';

// study header section
.main-header {
  background-color: $light-grey-background !important;
}

.study-header {
  h2 {
    margin-top: 100px !important;
    margin-left: 0;
    padding-left: 0;
  }

  h4 {
    font-size: 30px;
    font-weight: 700;
  }

  h5 {
    margin-top: 3rem;
    margin-bottom: 0;
  }

  .prerequisites {
    font-size: 24px;
    font-weight: bold;

    .topic {
      font-size: 24px;
      font-weight: 500;
      color: $dark-grey;
    }
  }

  .authors {
    font-size: 20px;
    font-weight: 400;
    color: $dark-grey;
  }

  .topic-filters {
    background-color: $brand-black;
    width: 10%;
    height: 2rem;
    line-height: 1rem;
    color: white;
    margin-left: 2rem;
  }

  .study-navigation {
    width: 68%;
    align-items: flex-start;

    button.btn {
      width: 25%;
      margin-right: 1rem;
      font-size: 18px;
      font-weight: 700;
      border: 1px solid $brand-purple;
      background-color: $white;

      &:hover,
      &:active {
        background-color: $brand-purple;
        color: $white;
      }
    }
  }
}

// header divider
hr {
  margin-top: 0;
  margin-bottom: 0;
  background-color: transparent;
  border-top: 0.5px solid $light-grey;
}

// quiz questions
.quiz-header {
  display: flex;
  flex-direction: column;
}

.quiz-wrapper {
  margin-top: 1rem;
  margin-bottom: 1rem;
  margin-left: 0;

  h4 {
    font-size: 30px;
    font-weight: 700;
  }

  .question-text,
  .answer-text {
    font-size: 24px;
    font-weight: 400;
  }

  .answers-col-1,
  .answers-col-2 {
    display: flex;
    align-items: center;
    margin-top: 1rem;
    padding-left: 0;

    button {
      display: flex;
      align-items: center;
      margin-left: 0;
      margin-right: 2rem;
      margin-bottom: 0.5rem;
      font-size: 18px;
      font-weight: 500;
      background-color: $white;
      height: 74px;
      width: 505px;
      box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);
      border-radius: 10px;

      &:hover {
        color: $white;
        background-color: $brand-purple;
      }

      &:hover .circle {
        color: $brand-purple;
        background-color: $white;
      }

      .circle {
        font-size: 22px;
        font-weight: 500;
        border-radius: 50%;
        height: 42px;
        width: 42px;
        color: $white;
        background-color: $brand-purple;
      }

      .answer-text {
        margin-left: 1rem;
      }
    }
  }
}
