@import 'globals/variables';

.summarization-step-1 {
  .summary-wrapper {
    padding-left: 0;

    .summary-col-1 {
      display: flex;
      margin-left: 0;
      text-align: center;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 4px dashed #8d8d8d;
      box-sizing: border-box;
      border-radius: 8px;

      .upload-prompt {
        position: absolute;
        justify-content: center;
        top: 32rem;
        left: 0.5rem;
      }
    }

    .summary-col-2 {
      h3 {
        font-weight: bold;
        font-size: 35px;
        line-height: 49px;
      }

      p {
        font-size: 22px;
        font-weight: 400;
      }

      .button-wrapper {
        margin-top: 3rem;

        .btn-instructions {
          margin-right: 0.5rem;
          margin-left: 0.5rem;
          padding-bottom: 1.5rem;
          justify-content: space-between;
          font-size: 18px;
          font-weight: 700;

          a {
            text-decoration: underline;
            margin-left: auto;
            margin-right: 0.5rem;
          }
        }
      }
    }
  }
}
