@import 'globals/variables';

  /*  -- non registered summary section --  */
  .summary-section {

    .non-registered {
      display: flex;
      flex-direction: column;
      
      .summary-col-1 {
        background: linear-gradient($white, grey);
        z-index:9;
        
        .text-cover{
          position: absolute;
          // opacity:1;
          background: linear-gradient(to bottom, rgba(255,0,0,0), rgba(139,139,139,1));
          z-index:5;
          top:0;
          left:0;
          width:100%;
          height:100%;
        }
      }

      .summary-col-2 {
        h4 {
          font-weight: bold;
          margin-bottom: 1.5rem;
        }
        span {
          font-size: 1.8rem;
          font-weight: bold;
        }

        .btn-primary {
          margin-top: 2rem;
        }
      }
    }
  }