@import 'globals/variables';
@import 'globals/mixins';

/* -- main header -- */
.main-header {
  background-color: $white !important;

  .container {
    margin-top: 14px;

    h2 {
      padding: 0 0;
      margin-top: 58px;
      font-size: 48px;
      font-weight: 700;
    }
  }
}

/* -- main section -- */
.main-content-landing {
  padding-bottom: 0rem !important;
  @include background('/assets/green_blob.svg');

  background-color: #cecece;

  .hero-wrapper {
    margin-top: 86px;

    .hero-header-wrapper-landing {
      flex: 3;
      margin-top: 5rem;

      h1 {
        font-size: 65px;
        font-weight: 700;
        line-height: 80px;
        padding-left: 1rem;
      }
    }

    /* -- new tech image --  */
    .hero-img-wrapper {
      .hero-img {
        margin-top: 5rem;
        z-index: 10;
        margin-left: 4rem;
      }
    }
  }

  /*  -- summary section --  */
  .summary-section-landing {
    width: 100%;
    top: 592px;
    margin-top: 2rem;
    padding-bottom: 2rem;
    border-radius: 0.5rem;
    background-color: $white;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.1);

    .summary-header-landing h2 {
      margin-top: 58px;
      text-align: center;
      margin: auto;
      font-size: 44px !important;
      font-weight: 700;
    }
    .summary-wrapper {
      display: flex;
      box-shadow: none;
    }
    .summary-col-1 {
      display: flex;
      text-align: center;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 50%;
      height: 450px;
      border: 4px dashed #8d8d8d;
      box-sizing: border-box;
      border-radius: 8px;

      .upload-prompt {
        position: absolute;
        justify-content: center;
        top: 24rem;
        left: 0.5rem;
      }
    }
    .summary-col-2 {
      display: flex;
      flex: 1;
      flex-direction: column;
      justify-content: center;
      width: 50%;
      padding: 0 4rem;

      h5 {
        color: #5222d0;
        font-weight: 500;
        font-size: 24px;
        padding-bottom: 1rem;
      }

      .skip-summary {
        font-weight: 700;
        font-size: 18px;

        a {
          color: #5222d0;
          text-decoration: underline;
        }
      }
    }
  }

  // character data section
  .character-data-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 15rem;

    h2 {
      font-weight: 700;
      font-size: 44px;
      line-height: 0px;
      margin-top: 5rem;
      margin-bottom: 0;
      padding-bottom: 0;
    }

    .character-number {
      font-style: normal;
      font-weight: 500;
      font-size: 240px;
      line-height: 293px;
      text-align: center;
    }

    p {
      font-size: 24px;
      line-height: 28px;
      padding-top: 1rem;
    }
  }

  // testimonials section
  .testimonial-wrapper {
    @include background('/assets/bottom_green_blob.svg');
    margin-top: 86px;
    background-size: cover;
    height: 900px;

    .testimonial-row {
      padding: 2rem;

      .testimonial-header {
        display: flex;
        flex-direction: column;
        flex: 2;
        margin-top: 5rem;

        h2 {
          font-size: 44px;
          font-weight: 700;
        }

        .testimonial-copy {
          font-style: italic;
          font-weight: normal;
          font-size: 46px;
          line-height: 53px;
          color: #6f6f6f;
        }
        .testimonial-author {
          font-size: 18px;
          line-height: 21px;
          color: #6f6f6f;
        }

        .testimonial-nav {
          margin-top: 1rem;
          padding-left: 0.8rem;

          .nav-item {
            margin-right: 0.5rem;
            width: 11px;
            height: 11px;
            border-radius: 50%;
            background: #a3a3a3;
          }

          .active-green {
            background: $brand-green;
          }
        }
      }
    }

    /* -- new tech image --  */
    .testimonial-img-wrapper {
      flex: 2;

      .testimonial-img {
        margin-top: 5rem;
        z-index: 10;
        margin-left: 4rem;
      }
    }
  }

  .btn-primary {
    color: $brand-black;
    border-radius: 5px;
    font-weight: bold;
    width: 15rem;
    margin-right: 1rem;
    background-color: $white !important;
    border-color: $brand-green !important;
    box-shadow: 3px 3px 0 0 $brand-green;

    &:hover {
      color: $white;
      background-color: $brand-green !important;
      box-shadow: 3px 3px 0 0 $brand-black;
    }
  }

  .btn-secondary {
    color: $brand-black;
    font-weight: bold;
    border-radius: 5px;
    width: 15rem;
    background-color: $white !important;
    border-color: $brand-black !important;
    box-shadow: 3px 3px 0 0 $brand-black;

    &:hover {
      color: $white;
      background-color: $brand-black !important;
    }
  }
}
